.App {
  text-align: center;
  background-image: url("smallbusin.jpg");
  background-size: cover;
}

.square{ 
  background-color: rgba(0, 0, 0, 0.7);
  max-width: 50%;
  max-height: 50%;

}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
